.carousel {
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(39, 41, 39, 0.8)),
    url(./testimonies.jpg);
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
}


.carousel p {
  text-align: center;
  color: white;
  margin-top: 40px;
  background-color: rgb(42, 51, 75);
  
   
}

.carousel-indicators li {
  display: none;
}

.carousel h1 {
  text-align: center;
  color: white;
  
}


.testimony-header {
  text-align: center;
  color: white;
  margin-bottom: 50px;
  margin-top: 100px;
}

#testimony-header-text {
  font-size: 25px;
}
